import React, { useState } from 'react';
import GooglePayButton from '@google-pay/button-react';

export default function App() {
  function checkCanMakePayment(request) {
    // If canMakePayment() isn't available, default to assume the method is
    // supported.
    var canMakePaymentPromise = Promise.resolve(true);

    // Feature detect canMakePayment().
    if (request.canMakePayment) {
      canMakePaymentPromise = request.canMakePayment();
    }

    return canMakePaymentPromise
      .then((result) => {
        // Store the result in cache for future usage.
        return result;
      })
      .catch((err) => {
        console.log('Error calling canMakePayment: ' + err);
      });
  }

  function checkout() {
    const supportedInstruments = [
      {
        supportedMethods: 'https://tez.google.com/pay',
        data: {
          pa: 'merchant-vpa@xxx',
          pn: 'Merchant Name',
          tr: '1234ABCD', // your custom transaction reference ID
          url: 'http://url/of/the/order/in/your/website',
          mc: '1234', // your merchant category code
          tn: 'Purchase in Merchant',
          gstBrkUp: 'GST:16.90|CGST:08.45|SGST:08.45', // GST value break up
          invoiceNo: 'BillRef123', // your invoice number
          invoiceDate: '2019-06-11T13:21:50+05:30', // your invoice date and time
          gstIn: '29ABCDE1234F2Z5', // your GSTIN
        },
      },
    ];

    const details = {
      total: {
        label: 'Total',
        amount: {
          currency: 'INR',
          value: '10.01', // sample amount
        },
      },
      displayItems: [
        {
          label: 'Original Amount',
          amount: {
            currency: 'INR',
            value: '10.01',
          },
        },
      ],
    };

    if (window.PaymentRequest) {
      console.log('Web payments are not supported in this browser.');
      return;
    }

    let request = null;
    try {
      request = new PaymentRequest(supportedInstruments, details);
    } catch (e) {
      console.log('Payment Request Error: ' + e.message);
      return;
    }
    if (!request) {
      console.log('Web payments are not supported in this browser.');
      return;
    }

    var canMakePaymentPromise = checkCanMakePayment(request);
    canMakePaymentPromise
      .then((result) => {
        showPaymentUI(request, result);
      })
      .catch((err) => {
        console.log('Error calling checkCanMakePayment: ' + err);
      });
  }

  function showPaymentUI(request, canMakePayment) {
    // Set payment timeout.
    let paymentTimeout = window.setTimeout(function () {
      window.clearTimeout(paymentTimeout);
      request
        .abort()
        .then(function () {
          console.log('Payment timed out after 20 minutes.');
        })
        .catch(function () {
          console.log('Unable to abort, user is in the process of paying.');
        });
    }, 20 * 60 * 1000); /* 20 minutes */

    request
      .show()
      .then(function (instrument) {
        window.clearTimeout(paymentTimeout);
        console.log(instrument);
      })
      .catch(function (err) {
        console.log(err);
      });
  }

  return <div>{ <button onClick={checkout}>buy now</button> }</div>;
}